.hero-img{
    width: 100%;
    background: rgba(0, 0,0,0.5);
    height: 60vh;
    position: relative;
}
.hero-img::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1509718443690-d8e2fb3474b7?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTV8fHByb2dyYW1taW5nfGVufDB8fDB8fHww&auto=format&fit=crop&w=800&q=60");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left:0;
    z-index: -1;
}
.hero-img h1{
    font-size: 2.4rem;
}
.hero-img p{
    text-align: center;
    font-size: 1.4rem;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@media screen and (max-width:640px){
    .hero-img h1{
        font-size: 2rem;
    }
}